"use client";

import { usePageStore } from "@/store/page";
import { useProfileDropdownStore } from "@/store/profileDropdown";
import { useLoggedInStore } from "@/store/loggedIn";

import { LinkELement } from "./LinkElement";
import { ProfileDropdownElement } from "./ProfileDropdownElement";

import { GoPerson } from "react-icons/go";
import { PiGear } from "react-icons/pi";
import { MdOutlineLogin } from "react-icons/md";
import { MdLogout } from "react-icons/md";

const Header = () => {
  const isProfileDropdownOpen = useProfileDropdownStore(
    (state) => state.isProfileDropdownOpen,
  );
  const toggleProfileDropdown = useProfileDropdownStore(
    (state) => state.toggleProfileDropdown,
  );
  const isLoggedIn = useLoggedInStore((state) => state.isLoggedIn);

  return (
    <nav className="fixed top-12 z-50 flex w-full items-center justify-center">
      <div className="relative flex w-full flex-row items-center justify-center">
        <div
          className={
            "flex h-18 items-center justify-between gap-6 rounded-full bg-black/10 px-8 shadow-lg ring-1 ring-white/10 backdrop-blur-2xl"
          }
        >
          <LinkELement title={"landingPage"} />
          <span className="hidden sm:flex">
            <LinkELement title={"tryForFreePage"} />
          </span>
          <LinkELement title={"pricingPage"} />
        </div>
        <div
          className="absolute right-10 flex h-10 w-10 cursor-pointer items-center justify-center rounded-full bg-black/10 text-center font-medium text-[oklch(0.95_0_0)] shadow-lg ring-1 ring-white/10 backdrop-blur-2xl transition-all duration-300 ease-in-out sm:right-18"
          onClick={toggleProfileDropdown}
        >
          TC
        </div>

        {isProfileDropdownOpen && isLoggedIn ? (
          <div className="absolute top-18 right-10 flex flex-col justify-center rounded-md bg-black/10 p-1.5 shadow-lg ring-1 ring-white/10 backdrop-blur-2xl transition-all duration-300 ease-in-out sm:right-18">
            <ProfileDropdownElement title={"profilePage"} Icon={GoPerson} />
            <ProfileDropdownElement title={"settingsPage"} Icon={PiGear} />
            <ProfileDropdownElement title={"logOutPage"} Icon={MdLogout} />
          </div>
        ) : isProfileDropdownOpen && !isLoggedIn ? (
          <div className="absolute top-18 right-18 flex flex-col justify-center rounded-lg bg-black/10 p-1.5 shadow-lg ring-1 ring-white/10 backdrop-blur-2xl">
            <ProfileDropdownElement title={"logInPage"} Icon={MdOutlineLogin} />
          </div>
        ) : null}
      </div>
    </nav>
  );
};

export default Header;
